<template>
  <div class="row">

    <div class="col s8">
      <!-- user search -->
      <div class="search-wrapper card" v-if="!repoNotClick">
        <form v-on:submit.prevent="searchClick">
          <input id="search" placeholder="Search by username" autocomplete="off" v-model="username" @keyup="onKeyUpSearch"> 
          <i class="material-icons" @click="searchClick">search</i>
        </form>
        <div v-show="searchDropdown">
          <ul class="search-list" v-if="username">
            <li v-for="result in searchedRepo" class="searchitem" :data-value="result.login" @click="searchedSingleUser">
              <img :src="result.avatar_url" width="25" height="25" :alt="result.login">
              <p>{{result.login}}</p>
              <!-- <pre>{{result}}</pre> -->
            </li>
          </ul>
        </div>
      </div>
      <!-- user search
      {{this.username}}
      {{selectedUser}}
      <pre>
        {{searchedRepoError}}
      </pre>--> 

      

      <!-- repo list -->
      <div v-if="!repoNotClick">

        <ul class="collection with-header">
          <li class="collection-header">
            <h5>Git Repos</h5>
          </li>

          <paginate name="repos" :list="repos" :per="3" class="paginate-list">
            <li class="collection-item avatar" v-for="repo in paginated('repos')">
              <i class="material-icons circle">folder</i>
              <span class="title">{{repo.name}}</span>
              <p>{{repo.description}} <br>
                <strong>{{repo.fork ? 'Forked repo' : 'Not a forked repo'}}</strong>
              </p>
              <a :href="repo.name" @click="repoClick($event)" class="secondary-content"><i class="material-icons">send</i></a>
            </li>
          </paginate>

        </ul>

        <paginate-links for="repos" :show-step-links="true" 
          :classes="{
            'ul': 'pagination',
            'ul li': 'waves-effect',
            '.prev > a': ['prev-link', 'another-class'] // multiple classes
          }"
        ></paginate-links>

      </div>
      <!-- repo list -->

      <!-- single repo -->
      <div v-if="repoNotClick">
        <ul class="collection with-header">
          <li class="collection-header">
            <h4 class="singlerepoheading">{{singleRepo.name}}</h4> 
            <a class="waves-effect waves-light red btn backbtn" @click="singleRepoBack">Back</a>
          </li>
          <li class="collection-item">{{singleRepo.description}}</li>
          <!-- <li class="collection-item"><strong>Creator Username: </strong> {{singleRepo.owner.login}}</li> -->
          <li class="collection-item"><strong>{{singleRepo.fork ? 'Forked repo' : 'Not a forked repo'}}</strong></li>
          <li class="collection-item"><strong>Last updated: </strong> {{singleRepo.updated_at | moment("dddd, MMMM Do YYYY")}}</li>
          <li class="collection-item">
            <a class="btn waves-effect waves-light" :href="singleRepo.html_url" target="_blank">
              <span>View</span>
              <i class="material-icons right">send</i>
            </a>
          </li>
        </ul>
      </div>
      <!-- single repo -->
    </div>

    <!-- sidebar user details -->
    <div class="col s4">
      
      <h5>User Details</h5>
      <div class="card">
        <div class="card-image waves-effect waves-block waves-light">
          <img class="activator" :src="userDetails.avatar_url">
        </div>
        <div class="card-content">
          <span class="card-title activator grey-text text-darken-4">{{userDetails.login}}<i class="material-icons right">more_vert</i></span>
          <p><a :href="userDetails.html_url" target="_blank">Profile</a></p>
        </div>
        <div class="card-reveal">
          <span class="card-title grey-text text-darken-4">Bio<i class="material-icons right">close</i></span>
          <p><strong>Name: </strong>{{userDetails.name}}</p>
          <p><strong>Company: </strong>{{userDetails.company}}</p>
          <p><strong>Location: </strong>{{userDetails.location}}</p>
          <p><strong>Following: </strong><span data-badge-caption="" class="new badge blue">{{userDetails.following}}</span></p>
          <p><strong>Followers: </strong><span data-badge-caption="" class="new badge red">{{userDetails.followers}}</span></p>
        </div>
      </div>

    </div>
    <!-- sidebar user details -->

  </div>
</template>

<script>
import axios from 'axios';
import vSelect from "vue-select"

let userDetailsUrl= 'https://api.github.com/users/';
let userRepos= 'https://api.github.com/repos/';
let defultUserName= 'subhojit1992';

export default {
  name: 'home',
  components: {vSelect},
  data () {
    return {
      repoNotClick: false,
      searchDropdown: false,
      searchedRepo: [],
      searchedRepoError: [],
      selectedUser: '',
      repos: [],
      singleRepo: [],
      singleRepoError: [],
      errors: [],
      userDetails: [],
      userDetailsError: [],
      paginate: ['repos'],
      username: '',
      defultName: defultUserName
    }
  },

  // life cycle method
  created(){
    // progress bar
    this.$Progress.start();

    // defult user and repo
    let userAddedRepo = userDetailsUrl + this.defultName + '/repos';
    let userDetails = userDetailsUrl + this.defultName;

    // get repos url
    axios.get(userAddedRepo)
      .then((response) => {
        this.repos = response.data
      })
      .catch((error) => {
        this.errors.push(error);
      });

    // get user details
    axios.get(userDetails)
      .then((response) => {
        this.userDetails = response.data
      })
      .catch((error) => {
        this.userDetailsError.push(error);
      });

  },

  // mounted life cycle
  mounted () {
    //  progress finished
    this.$Progress.finish();
  },

  // methods
  methods: {
    // repo click
    repoClick($event){
      $event.preventDefault();
      const getElm = $event.currentTarget;
      const href = getElm.getAttribute('href');

      // making request link
      let singleRepo= userRepos + this.defultName;
      let singleUserRepo= singleRepo + '/' + href;
      
      // set the value true
      this.repoNotClick = true;

      // progress started
      this.$Progress.start();

      // get user details
      axios.get(singleUserRepo)
        .then((response) => {
          this.singleRepo = response.data;
          this.$Progress.finish();
        })
        .catch((error) => {
          this.singleRepoError.push(error);
          this.$Progress.fail();
        });

      // console.log(this.repoNotClick );
    },

    // single repo back
    singleRepoBack($event){
      $event.preventDefault();
      this.repoNotClick = false;
      this.singleRepo = [];
    },

    // search click
    searchClick($event){

      // defult user and repo
      let currentUserDetails = userDetailsUrl + this.username;
      let currentUserAddedRepo = userDetailsUrl + this.username + '/repos';

      // defult name changes
      this.defultName = this.username;

      // loading started
      this.$Progress.start();

      // get user details
      axios.get(currentUserDetails)
        .then((response) => {
          this.userDetails = response.data;
          // loading finish
          this.$Progress.finish();
        })
        .catch((error) => {
          this.userDetailsError.push(error);
          // loading fail
          this.$Progress.fail();
        });

      
      // get repos url
      axios.get(currentUserAddedRepo)
        .then((response) => {
          this.repos = response.data;
        })
        .catch((error) => {
          this.errors.push(error);
        });  


      

    },

    // on blur search
    onKeyUpSearch($event){
      this.searchDropdown = true;
      // get user details
      axios.get('https://api.github.com/search/users?q=' + this.username)
        .then((response) => {
          this.searchedRepo = response.data.items;
          // console.log(this.searchedRepo);
        })
        .catch((error) => {
          this.searchedRepoError.push(error);
          // console.log(this.searchedRepoError[0].response);
          var $toastContent = $('<p>' + this.searchedRepoError[0].response.data.message + '</p>');
          if(this.searchedRepoError[0].response.status == 403 ){
           Materialize.toast($toastContent, 6000);
          }
          
        });
    },

    // searched user click
    searchedSingleUser($event){
      const getElm = $event.currentTarget;
      const dataVal = getElm.getAttribute('data-value');

      this.selectedUser = dataVal;
      this.username = this.selectedUser;
      this.searchDropdown = false;

      // defult name changes
      this.defultName = this.username;


      this.searchClick();
    }

    


  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.singlerepoheading{
  display: inline-block;
}
.backbtn{
  float: right;
  margin-top: 15px;
}
.collection.with-header 
.collection-item.avatar {
    padding-right: 80px;
}
.search-wrapper {
    margin: 12px 0;
    transition: margin .25s ease;
}
.search-wrapper input#search {
    display: block;
    font-size: 16px;
    font-weight: 300;
    width: 100%;
    height: 45px;
    margin: 0;
    box-sizing: border-box;
    padding: 0 45px 0 15px;
    border: 0;
}
.search-wrapper i.material-icons {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
}
.search-list{
  height: 200px;
  background: #eee;
  margin: 0;
  position: absolute;
  z-index: 1;
  width: 100%;
  overflow: auto;
}
.searchitem{
  border-bottom: 1px solid #fff;
  padding: 10px;
}
.searchitem:hover{
  background: #ee6e73;
  color: #fff; 
  cursor: pointer;
}
.searchitem:hover p{
  font-weight: 700;
}
.searchitem:last-child{
  border-bottom: 0px;
}
.searchitem:after,
.searchitem:before{
  content: '';
  display: block;
  clear: both;
}
.searchitem img{
  float: left;
  margin-right: 20px;
}
.searchitem p{
  float: left;
  margin: 0;
  line-height: 25px;
}
</style>



